async function query(filterBy) {
  if (!filterBy) return books;
  var booksToReturn = [...books];
  if (filterBy.name) {
    booksToReturn = books.filter(book =>
      book.title.toLowerCase().includes(filterBy.name.toLowerCase())
    );
  }
  if (filterBy.fromPrice) {
    booksToReturn = booksToReturn.filter(book => book.listPrice.amount >= filterBy.fromPrice);
  }
  if (filterBy.toPrice) {
    booksToReturn = booksToReturn.filter(book => book.listPrice.amount <= filterBy.toPrice);
  }
  return booksToReturn;
}

async function get(id) {
  return books.find(book => book.id === id);
}

async function addReview(review, bookId) {
  const reviewToAdd = { ...review, id: _makeId() };
  const bookIdx = books.findIndex(book => book.id === bookId);
  if (books[bookIdx].reviews) books[bookIdx].reviews.unshift(reviewToAdd);
  else books[bookIdx].reviews = [reviewToAdd];
  localStorage.setItem(KEY, JSON.stringify(books));
  return books[bookIdx];
}

async function removeReview(reviewId, bookId) {
  const bookIdx = books.findIndex(book => book.id === bookId);
  books[bookIdx].reviews = books[bookIdx].reviews.filter(review => review.id !== reviewId);
  localStorage.setItem(KEY, JSON.stringify(books));
  return books[bookIdx];
}

async function queryApi(q) {
  const res = await axios.get(`https://www.googleapis.com/books/v1/volumes?printType=books&q=${q}`);
  return res.data.items;
}

async function addApiBook(rawBook) {
  if (books.some(book => book.id === rawBook.id)) return null;
  const bookToAdd = _formatBook(rawBook);
  books.unshift(bookToAdd);
  localStorage.setItem(KEY, JSON.stringify(books));
  return bookToAdd;
}

export const bookService = {
  query,
  get,
  addReview,
  removeReview,
  queryApi,
  addApiBook
};

function _makeId(length = 6) {
  let id = '';
  const possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';

  for (let i = 0; i < length; i++) {
    id += possible.charAt(Math.floor(Math.random() * possible.length));
  }

  return id;
}

function _formatBook(rawBook) {
  rawBook = JSON.parse(JSON.stringify(rawBook));
  return {
    id: rawBook.id,
    title: rawBook.volumeInfo.title,
    subtitle: rawBook.volumeInfo.subtitle || '',
    authors: rawBook.volumeInfo.authors || [],
    publishedDate: rawBook.volumeInfo.publishedDate,
    description: rawBook.volumeInfo.description,
    pageCount: rawBook.volumeInfo.pageCount,
    categories: rawBook.volumeInfo.categories || [],
    thumbnail: rawBook.volumeInfo.imageLinks.smallThumbnail || '', //should I add a default img?
    language: rawBook.volumeInfo.language,
    listPrice:
      rawBook.saleInfo && rawBook.saleInfo.listPrice
        ? {
            amount: rawBook.saleInfo.listPrice.amount,
            currencyCode: rawBook.saleInfo.listPrice.currencyCode,
            isOnSale: Math.random > 0.5
          }
        : null
  };
}
