<template>
  <section class="book-add container">
    <h1 class="title">Add a new book</h1>
    <div class="search-field flex align-center justify-center">
      <input
        @keyup.enter="onSearch"
        class="search-book-input"
        type="search"
        v-model="name"
        placeholder="Search for a book.."
      />
      <button @click="onSearch" class="search-btn">Search</button>
    </div>
    <div class="books-gallery">
      <div v-if="loading" class="lds-ellipsis">
        <div></div>
        <div></div>
        <div></div>
        <div></div>
      </div>
      <book-list :apiMode="true" :books="apiBooks" />
    </div>
  </section>
</template>

<script>
import { bookService } from '../services/bookService';
import bookList from '../components/book-list';
import { eventBus } from '../services/eventBusService';
export default {
  name: 'book-add',
  data() {
    return {
      name: '',
      loading: false,
      apiBooks: []
    };
  },
    created() {
    eventBus.$on('add-book', this.addBook);
  },
  destroyed() {
    eventBus.$off('add-book', this.addBook);
  },
  methods: {
    async onSearch() {
      this.loading = true;
      this.apiBooks = await bookService.queryApi(this.name);
      this.loading = false;
    },
    async addBook(rawBook) {
      const addedBook = await bookService.addApiBook(rawBook);
      if (!addedBook) {
        eventBus.$emit('user-msg', { txt: 'It appears that this book already exists..' });
        return;
      }
      eventBus.$emit('user-msg', {
        txt: 'Book added successfully!',
        link: `/book/${addedBook.id}`
      });
    }
  },
  components: {
    bookList
  },
};
</script>
