<template>
  <article class="review-card">
    <div class="review-header">
      <h2 class="title">{{ review.title }}</h2>
      <div class="stars-cont">
        <span class="star" v-for="(star, idx) in review.rate" :key="idx"></span>
      </div>
      <button class="remove-btn" @click="removeReview"></button>
    </div>
    <span class="review-name">{{ review.fullName | capitalize }}, Read at {{ review.readAt }}</span>
    <long-txt class="review-desc" :txt="description" chars="65" />
  </article>
</template>

<script>
import LongTxt from './long-txt';
import { eventBus } from '../services/eventBusService';
export default {
  name: 'review-preview',
  props: ['review'],
  computed: {
    description() {
      return `"${this.review.desc}"`;
    }
  },
  methods: {
    removeReview() {
      this.$store.dispatch({ type: 'removeReview', reviewId: this.review.id });
      eventBus.$emit('user-msg', { txt: 'Review deleted successfully!' });
    }
  },
  filters: {
    capitalize(str) {
      if (!str) return '';
      str = str.toString();
      if (Array.isArray(str.split(' '))) {
        return str
          .split(' ')
          .map(word => word.charAt(0).toUpperCase() + word.slice(1))
          .join(' ');
      }
      return str.charAt(0).toUpperCase() + str.slice(1);
    }
  },
  components: {
    LongTxt
  }
};
</script>
