<template>
  <div class="book-page container">
    <book-filter />
    <book-list :apiMode="false" :books="booksToShow" />
  </div>
</template>

<script>
import BookList from '../components/book-list';
import BookFilter from '../components/book-filter';

export default {
  name: 'BookApp',
  computed: {
    booksToShow() {
      return this.$store.getters.booksToShow;
    }
  },
  async created() {
    this.books = await this.$store.dispatch({ type: 'loadBooks' });
  },
  components: {
    BookList,
    BookFilter
  }
};
</script>
