<template>
  <div :class="{ shown: msg }" class="user-msg flex align-center">
    <div class="icon"></div>
    <div v-if="msg" class="msg-content">
      <p class="txt">{{ msg.txt }}</p>
      <router-link v-if="msg.link" :to="msg.link" class="link">Check it out!</router-link>
    </div>
    <button @click="resetMsg" class="close-btn"></button>
  </div>
</template>

<script>
import { eventBus } from '../services/eventBusService';
export default {
  name: 'user-msg',
  created() {
    eventBus.$on('user-msg', msg => {
      this.msg = msg;
    });
  },
  destroyed() {
    eventBus.$off('user-msg');
  },
  data() {
    return {
      msg: null,
      msgTimeout: null
    };
  },
  methods: {
    resetMsg() {
      this.msg = null;
      if (this.msgTimeout) clearTimeout(this.msgTimeout);
    }
  },
  watch: {
    msg() {
      if (this.msg) {
        if (this.msgTimeout) clearTimeout(this.msgTimeout); //For 2+ msgs in a row
        this.msgTimeout = setTimeout(() => {
          this.resetMsg();
        }, 3500);
      }
    },
    $route() {
      //This will make the msg disappear when changing routes
      this.resetMsg();
    }
  }
};
</script>
