<template>
  <router-link v-if="!apiMode" :to="`/book/${book.id}`">
    <article class="book-card flex column align-center">
      <div class="book-img"><img :src="book.thumbnail" alt="book" /></div>
      <h4 class="title">{{ book.title }}</h4>
      <p>{{ priceToDisplay }}</p>
    </article>
  </router-link>
  <!--this will be presented at add-book -->
  <article v-else class="book-card flex column align-center">
    <div
      v-if="book.volumeInfo.imageLinks && book.volumeInfo.imageLinks.smallThumbnail"
      class="book-img"
    >
      <img :src="book.volumeInfo.imageLinks.smallThumbnail" alt="book" />
    </div>
    <h4 class="title">{{ book.volumeInfo.title }}</h4>
    <button class="add-btn" @click="addBook">Add Book</button>
  </article>
</template>

<script>
import { eventBus } from '../services/eventBusService';
export default {
  name: 'book-preview',
  props: ['book', 'apiMode'],
  computed: {
    currency() {
      if (!this.book.listPrice) return null;
      switch (this.book.listPrice.currencyCode.toUpperCase()) {
        case 'ILS':
          return '';
        case 'USD':
          return '$';
        default:
          return '$';
      }
    },
    priceToDisplay() {
      if (!this.book.listPrice) return 'Currently unavailable';
      return this.currency === '$'
        ? `${this.currency}${this.book.listPrice.amount}`
        : `${this.book.listPrice.amount}${this.currency}`;
    }
  },
  methods: {
    addBook() {
      eventBus.$emit('add-book', this.book);
    }
  }
};
</script>
