<template>
  <section v-if="book" class="book-details-page container flex column">
    <div class="flex">
      <div class="sale-sign" v-if="book.listPrice && book.listPrice.isOnSale"></div>
      <div class="tech-details">
        <div class="book-img">
          <img :src="book.thumbnail" :alt="book.title" />
        </div>
        <p class="published"><span>Published: </span>{{ howOld }}</p>
        <p class="read-period"><span>Reading period: </span>{{ readingPeriod }}</p>
        <p class="lang"><span>Language: </span>{{ book.language }}</p>
      </div>
      <div class="book-details">
        <h1 class="title">{{ book.title }}</h1>
        <h2 class="subtitle">{{ book.subtitle }}</h2>
        <p class="authors">
          By <span class="names">{{ authors }}</span>
        </p>
        <p class="desc">
          <long-txt :txt="book.description || 'No description available'" chars="60" />
        </p>
        <div class="categories flex wrap">
          <span v-for="(category, idx) in book.categories" :key="idx" class="category">{{
            category
          }}</span>
        </div>
        <p class="price">
          Price:
          <span
            v-if="book.listPrice"
            :class="{
              green: this.book.listPrice.amount < 20,
              red: this.book.listPrice.amount > 150
            }"
            >{{ priceToDisplay }}</span
          >
          <span v-else>{{ priceToDisplay }}</span>
        </p>
      </div>
    </div>
    <review-list v-if="book.reviews" :reviews="book.reviews"></review-list>
    <review-add @addReview="addReview" />
  </section>
</template>

<script>
import LongTxt from '../components/long-txt';
import ReviewAdd from '../components/review-add';
import ReviewList from '../components/review-list';
import { eventBus } from '../services/eventBusService';
export default {
  name: 'book-details',
  computed: {
    book() {
      return this.$store.getters.book;
    },
    authors() {
      if (this.book.authors.length < 1) return 'Unknown';
      return this.book.authors.join(', ');
    },
    readingPeriod() {
      if (this.book.pageCount < 100) return 'Light Reading';
      else if (this.book.pageCount < 200) return 'Decent Reading';
      else return 'Long Reading';
    },
    howOld() {
      const diff = new Date(Date.now()).getFullYear() - this.book.publishedDate;
      if (diff >= 10) return 'Veteran Book';
      else if (diff <= 1) return 'New!';
      return this.book.publishedDate;
    },
    currency() {
      if (!this.book.listPrice) return null;
      switch (this.book.listPrice.currencyCode.toUpperCase()) {
        case 'ILS':
          return '';
        case 'USD':
          return '$';
        default:
          return '$';
      }
    },
    priceToDisplay() {
      if (!this.book.listPrice) return 'Currently unavailable';
      return this.currency === '$'
        ? `${this.currency}${this.book.listPrice.amount}`
        : `${this.book.listPrice.amount}${this.currency}`;
    }
  },
  methods: {
    async loadBook() {
      const id = this.$route.params.id;
      await this.$store.dispatch({ type: 'getBookById', id });
    },
    addReview(review) {
      this.$store.dispatch({ type: 'addReview', review, bookId: this.book.id });
      eventBus.$emit('user-msg', { txt: 'Review added successfully!' });
    }
  },
  created() {
    this.loadBook();
  },
  watch: {
    '$route.params.id'() {
      this.loadBook();
    }
  },
  components: {
    LongTxt,
    ReviewAdd,
    ReviewList
  }
};
</script>
