import { bookService } from '../../services/bookService';

export const bookModule = {
  state: {
    books: [],
    book: null,
    bookFilterBy: null
  },
  getters: {
    booksToShow(state) {
      if (!state.bookFilterBy) return state.books;
      var booksToReturn = [...state.books];
      if (state.bookFilterBy.name) {
        booksToReturn = state.books.filter(book =>
          book.title.toLowerCase().includes(state.bookFilterBy.name.toLowerCase())
        );
      }
      if (state.bookFilterBy.fromPrice) {
        booksToReturn = booksToReturn.filter(
          book => book.listPrice && book.listPrice.amount >= state.bookFilterBy.fromPrice
        );
      }
      if (state.bookFilterBy.toPrice) {
        booksToReturn = booksToReturn.filter(
          book => book.listPrice && book.listPrice.amount <= state.bookFilterBy.toPrice
        );
      }
      return booksToReturn;
    },
    book(state) {
      return state.book;
    }
  },
  mutations: {
    setBooks(state, { books }) {
      state.books = [...books];
    },
    setBook(state, { book }) {
      state.book = JSON.parse(JSON.stringify(book));
    },
    setFilter(state, { filterBy }) {
      state.bookFilterBy = { ...filterBy };
    }
  },
  actions: {
    async getBookById({ commit }, { id }) {
      const book = await bookService.get(id);
      commit({ type: 'setBook', book });
      return { ...book };
    },
    async loadBooks({ commit }) {
      const books = await bookService.query();
      commit({ type: 'setBooks', books });
      return [...books];
    },
    setFilter({ commit }, { filterBy }) {
      commit({ type: 'setFilter', filterBy });
    },
    async addReview({ commit }, { review, bookId }) {
      const savedBook = await bookService.addReview(review, bookId);
      commit({ type: 'setBook', book: savedBook });
    },
    async removeReview({ commit, state }, { reviewId }) {
      const savedBook = await bookService.removeReview(reviewId, state.book.id);
      commit({ type: 'setBook', book: savedBook });
    }
  }
};
