<template>
  <div>
    <p>{{ txtToDisplay }}</p>
    <button v-if="this.txt.length > this.chars" class="show-more-btn" @click="toggleLongTxtShown">
      {{ btnTxt }}
    </button>
  </div>
</template>

<script>
export default {
  name: 'long-txt',
  props: ['txt', 'chars'],
  data() {
    return {
      isLongTxtShown: false
    };
  },
  computed: {
    txtToDisplay() {
      if (this.txt.length < this.chars) return this.txt;
      return this.isLongTxtShown ? this.txt : `${this.txt.slice(0, +this.chars)}...`;
    },
    btnTxt() {
      return this.isLongTxtShown ? 'Show less' : 'Show more';
    }
  },
  methods: {
    toggleLongTxtShown() {
      this.isLongTxtShown = !this.isLongTxtShown;
    }
  }
};
</script>
