<template>
  <form @submit.prevent="setFilter" class="filter">
    <h1 class="title">Search</h1>
    <input
      @input="setFilter"
      class="name-input"
      v-model.trim="filterBy.name"
      type="search"
      placeholder="Search by name.."
      spellcheck="false"
    />
    <label for="from">From price: </label>
    <input
      @input="setFilter"
      class="from-price"
      name="from"
      min="0"
      v-model.number="filterBy.fromPrice"
      type="number"
    />
    <label for="to">To price: </label>
    <input
      @input="setFilter"
      class="to-price"
      name="to"
      min="0"
      v-model.number="filterBy.toPrice"
      type="number"
    />
  </form>
</template>

<script>
export default {
  name: 'book-filter',
  data() {
    return {
      filterBy: {
        name: '',
        fromPrice: null,
        toPrice: null
      }
    };
  },
  methods: {
    setFilter() {
      this.$store.dispatch({ type: 'setFilter', filterBy: this.filterBy });
    }
  }
};
</script>
