<template>
  <section class="add-review">
    <h2 class="title">Write a review</h2>
    <form @submit.prevent="addReview" class="add-review-form flex column align-start">
      <h3 class="sub-title">Add a title</h3>
      <input
        class="title-input"
        type="text"
        spellcheck="false"
        autocomplete="off"
        name="title"
        maxlength="120"
        placeholder="Most important to know?"
        required
        v-model.trim="review.title"
      />
      <label for="fullName">Full name:</label>
      <input
        type="text"
        name="fullName"
        autocomplete="off"
        spellcheck="false"
        required
        maxlength="120"
        placeholder="John Doe"
        v-model.trim="review.fullName"
      />
      <label for="rate">Rate:</label>
      <select v-model.number="review.rate" name="rate">
        <option value="1">1</option>
        <option value="2">2</option>
        <option value="3">3</option>
        <option value="4">4</option>
        <option value="5">5</option>
      </select>
      <label for="readAt">Read at:</label>
      <input class="date-input" type="date" name="readAt" v-model="review.readAt" />
      <h3 class="sub-title">Write your review</h3>
      <textarea
        class="desc-input"
        autocomplete="off"
        spellcheck="false"
        name="desc"
        cols="50"
        rows="10"
        required
        placeholder="What did you like? What didn't you like?"
        v-model.trim="review.desc"
      />
      <button class="submit-btn">Publish</button>
    </form>
  </section>
</template>

<script>
export default {
  name: 'review-add',
  data() {
    return {
      review: {
        title: '',
        desc: '',
        rate: 1,
        fullName: 'Book Worm',
        readAt: this.getReviewDate()
      }
    };
  },
  methods: {
    addReview() {
      this.$emit('addReview', this.review);
      this.review = {
        title: '',
        desc: '',
        rate: 1,
        fullName: 'Book Worm',
        readAt: this.getReviewDate()
      };
    },
    getReviewDate() {
      const date = new Date();
      const day = date.getDate();
      const month = date.getMonth() < 10 ? '0' + date.getMonth() : date.getMonth();
      const year = date.getFullYear();
      return `${year}-${month}-${day}`;
    }
  }
};
</script>
